import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/scss/app.scss'
import Viewer from 'v-viewer'
import 'viewerjs/dist/viewer.css'
require("@/assets/js/jquery.fittext")
require("@/assets/js/jquery.lettering")
require("@/assets/js/jquery.textillate")
Vue.use(Viewer)
Vue.config.productionTip = false
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



