<template>
	<div class="page-case">
		<v-header curPge="case"></v-header>
		<div class="about">
			<div class="about-wrapper">
				<div class="about-us">产品案例</div>
				<div class="about-desc">产品&解决方案</div>
			</div>
		</div>
		<div class="case-list">
			<div class="case-item-wrapper">
				<ul class="flex-and-wrap">
					<li @click="detail('/detail/1','雅典娜')">
						<div class="card ydn">
							<div class="imgInfo">
								<h3>雅典娜</h3>
								<div class="mgt-50 mgb-70">
									<img src="../../assets/img/icons/icon-ydn.png">
								</div>
								<div class="case-desc">
									专注于为广大服务者提供法律帮助，同时也为律师提供一个学习和服务的平台。
								</div>
							</div>
						</div>
					</li>
					<li @click="detail('/detail/2','塔娜交易所')">
						<div class="card tana">
							<div class="imgInfo">
								<h3>塔娜交易所</h3>
								<div class="mgt-50 mgb-70">
									<img src="../../assets/img/icons/icon-tn.png">
								</div>
								<div class="case-desc">
									塔娜交易所采用分布式数据存储，通过去中心化的数据库，打通了交易所与多个货币平台的通道。
								</div>
							</div>
						</div>
					</li>
					<li  @click="detail('/detail/3','九九环保智能平台')">
						<div class="card jiujiu">
							<div class="imgInfo">
								<h3>九九环保智能平台</h3>
								<div class="mgt-50 mgb-70">
									<img src="../../assets/img/icons/icon-jiu.png">
								</div>
								<div class="case-desc">
									九九环保智能平台是大气污染源管、控、治一体化的智能平台。
								</div>
							</div>
						</div>
					</li>
					<li  @click="detail('/detail/4','新奥燃气管网监测系统可视化平台')">
						<div class="card xinao">
							<div class="imgInfo">
								<h3>新奥燃气管网监测系统可视化平台</h3>
								<div class="mgt-50 mgb-70">
									<img src="../../assets/img/icons/icon-xa.png">
								</div>
								<div class="case-desc">
									新奥燃气管网监测系统是通过可视化平台，在线监测设备报警情况等，及时处理报警设备，保证用户用气安全
								</div>
							</div>
						</div>
					</li>
					<li  @click="detail('/detail/5','欧特云智能巡检平台')">
						<div class="card zhinengxunjian">
							<div class="imgInfo">
								<h3>欧特云智能巡检平台</h3>
								<div class="mgt-50 mgb-70">
									<img src="../../assets/img/icons/icon-oty.png">
								</div>
								<div class="case-desc">
									欧特云智能巡检平台整合移动互联网、物联网和云计算技术为物业公司、设备运营商等客户提供数字化智能巡检云服务
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
		<v-footer />
	</div>	
</template>
<script>
	import Vue from 'vue'
	// import VueLazyload from 'vue-lazyload'
	import Header from "@/components/header.vue"
	import Footer from "@/components/footer.vue"
	// Vue.use(VueLazyload)
	export default{
		name:"case",
		components:{
      		"v-header":Header,
      		"v-footer":Footer
      	},
		data(){
			return {
				type:"all"
			}
		},
		mounted(){
			$('.about-us').textillate({ in: { effect: 'fadeInRight'} });
			$('.about-desc').textillate({ in: { effect: 'fadeInRight'} });
		},
		methods:{
			detail(path,caseName){
				this.$router.push(path);
				sessionStorage.caseName = caseName;
			},
			filterType(type){
				this.type = type;
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../assets/scss/mixin';
	.page-case{
		.about{
			width: 100%;
			height: px2vw(320);
			background: url("../../assets/img/icons/about.png") no-repeat;
			background-size: 100% 100%;

			.about-wrapper{
				width: 100%;
				height: 100%;
				background: rgba(0,12,63,0.7);
				padding-left: px2vw(370);
				text-align: left;
				padding-top: px2vw(84);
				color:rgba(255,255,255,1);
			}
			.about-us{
				font-size:px2vw(40);
				font-weight:bold;
			}
			.about-desc{
				font-size:px2vw(18);
				font-weight:400;
				margin-top: px2vw(16);
			}
		}
		.case-list{
			width: 100%;
			background: #EFF3F8;
			.case-item-wrapper{
				width: px2vw(1200);
				margin: 0 auto;
				padding: 20px 0;
				min-height: 500px;
				li{
					width: 25%;
					padding: 0 px2vw(10);
					height: 450px;
				}
				.card{
					width: px2vw(268);
					height: 430px;
					background: #fff;
					margin: 10px auto;
					background-size: 100% 100%;
					position: relative;
					.case-desc{
						font-size: px2vw(16);
						line-height: px2vw(28);
					}
					.imgInfo{
						position: absolute;
					    top: 0;
					    z-index: 2;
					    text-align: center;
					    width: 100%;
					    height: 100%;
					    color:#fff;
					    display: flex;
					    justify-content: center;
					    align-items: center;
					    flex-direction: column;
					    padding:0 10%;
					    transition: all .6s;
					    cursor: pointer;
					    background-color: rgba(63, 63, 74, 0.9);
					    img{
					    	transition: transform .6s;
					    }
					    &:hover{
							background-color: rgba(63, 63, 74, 0.4);

							img{
								transform: scale(1.1);
							}
					    }
					}
				}
				.ydn{
					background: url("../../assets/img/icons/yadianna.png") no-repeat;
				}
				.tana{
					background: url("../../assets/img/icons/tana.png") no-repeat;
				}
				.jiujiu{
					background: url("../../assets/img/icons/jiujiu.png") no-repeat;
				}
				.xinao{
					background: url("../../assets/img/icons/xinao.png") no-repeat;
				}
				.zhinengxunjian{
					background: url("../../assets/img/icons/zhinengxunjian.png") no-repeat;
				}
			}
		}
		// img[lazy=loading]{
		// 	background: url("../../assets/img/temp/loading.gif") center center no-repeat;
		// 	background-size: 100% 100%;
		// }
		
	}
</style>


